import React from 'react';
import './App.css';
import { GoodsList } from './components/GoodsList';

const goodsFromServer: string[] = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

type Props = {};

type State = {
  goodsList : string[],
  visibility : boolean,
};

class App extends React.Component <Props, State> {
  state = {
    goodsList: [...goodsFromServer],
    visibility: false,
  };

  visibilitySwitch = () => {
    this.setState((prevState) => ({
      visibility: !prevState.visibility,
    }));
  };

  displayGoods = () => (
    <ul>
      {this.state.goodsList.map(good => (
        <li key={good}>
          {good}
        </li>
      ))}
    </ul>
  );

  render() {
    return (
      <div className="App">
        <h1>Goods</h1>
        <br />
        {!this.state.visibility
          && (
            <button type="button" onClick={this.visibilitySwitch}>
              Start
            </button>
          )}
        {this.state.visibility && <GoodsList goods={this.state.goodsList} />}
      </div>
    );
  }
}

export default App;
